************* */
.signInModal {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 20vh;
    top: 175px;
    margin-left: auto;
    margin-right: auto;
    max-width: 360px;
    max-height: fit-content;
    background: linear-gradient(0deg, rgb(128, 104, 177) 0%, rgb(84, 185, 163) 100%);
    border-radius: 10px;
    padding: 40px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
}


.signUpBackground {
    height: 100vh;
    background: rgb(34,193,195);
    background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);
}

.signUpContainer {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 15vh;
    top: 175px;
    margin: 3rem;
    overflow: auto;
}

.closeIcon {
    float: right;
}

.logInForm {
    display: flex;
    flex-direction: column;
}

input {
    margin: .8rem;
    font-size: 15px;
}
textarea {
    width: 100%
    /* box-sizing: border-box; */
}

.formWrapper {
    display: grid;
    grid-template-columns: 1fr, 2fr;

}

.text-light{
    color:rgb(104, 104, 104)
}
.rounded-input {
    padding:8px;
    border-radius:10px;
}
.fullWidth {
    grid-column: 2   
}


select,
label {
    font-size: 10px;
}

/* **************
*               *
*    SIGNIN     *
*               *
************** */
.signUpModal, .signUpBackground {
    float: none;
    padding: 20px;
    vertical-align: top;
}
.inline{
    display: inline;
}

.signUpModal {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 20vh;
    top: 175px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 175px;
    max-width: fit-content;
    max-height: fit-content;
    background: linear-gradient(180deg, rgb(128, 104, 177) 0%, rgb(84, 185, 163) 100%);
    border-radius: 10px;
    padding: 20px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    overflow: auto;
}

div.signUpBackground {
    
    height: 1100px;
    background: rgb(34,193,195);
    background: linear-gradient(0deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%);

}

.userHobbies {
    background: linear-gradient(0deg, rgb(210, 207, 196) 0%, rgba(253,187,45,1) 100%);
    border-radius: 15px;
    padding: 1rem;
    margin: 2rem 8.5rem;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
}
.hobbyBtn {
    margin: .2rem;
    display: inline-block;
    color: white;
    text-transform: uppercase;
    font-size: 8px;
    padding: 0.25rem .5rem;
    border-radius: 2rem;
    cursor: pointer;
    transition: all 400ms ease-in;
    background: var(--color-accent-secondary);
}

.block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    text-align: right;
}

.div1 { grid-area: 1 / 1 / 2 / 2; }
.div2 { grid-area: 1 / 2 / 2 / 3; }
.div3 { grid-area: 2 / 1 / 3 / 2; }
.div4 { grid-area: 2 / 2 / 3 / 3; }
.div5 { grid-area: 3 / 1 / 4 / 2; }
.div6 { grid-area: 3 / 2 / 4 / 3; }
.div7 { grid-area: 4 / 1 / 5 / 3; }
.div8 { grid-area: 5 / 1 / 6 / 2; }
.div9 { grid-area: 6 / 1 / 7 / 2; }
.div10 { grid-area: 6 / 2 / 9 / 3; }

.hobbyModal {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 175px;
    background: linear-gradient(0deg, rgb(128, 104, 177) 0%, rgb(84, 185, 163) 100%);
    border-radius: 10px;
    padding: 40px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    justify-items: center;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.hobbiesContainer {
    padding: 20px;
    max-height: 200px;
    overflow: scroll;
  }

.hobbyCard {
    margin: .2rem;
    display: inline-block;
    color: white;
    text-transform: uppercase;
    font-size: 8px;
    padding: 0.25rem .5rem;
    border-radius: 2rem;
    cursor: pointer;
    transition: all 400ms ease-in;
    background: var(--color-dark);
}

textarea{
    grid-column-start: 1;
    grid-column-end: 2;
    /* width: 20rem; */
    height: 6rem;
}
.text-white {
    color: rgb(205, 204, 204);
}
.tab{
    padding-left: 3px;
    justify-content: center;
    align-items: center;
}
.editProfileModal {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 175px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 175px;
    max-width: fit-content;
    max-height: fit-content;
    background: linear-gradient(to bottom, #1f4037, #99f2c8);;
    border-radius: 10px;
    padding: 20px;
    box-shadow: rgba(0,0,0,0.35) 0px 5px 15px;
    overflow: auto;
}


/* *********************
*                      *
*    MEDIUM DEVICES    *
*                      *
********************* */
@media screen and (max-width: 1024px) {
    .signUpForm {
        display: grid;
    }
    .block {
        display: flex;
        flex-direction: column;
        text-align: right;
    }
    
}


/* ********************
*                     *
*    SMALL DEVICES    *
*                     *
******************** */
@media screen and (max-width: 600px) {
    .signUpForm {
        display: grid;
       
    }
    .block {
        display: flex;
        flex-direction: column;
        text-align: right;
    }
}